import { Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges, ViewEncapsulation } from '@angular/core';
import { MatIconModule } from '@angular/material/icon';
import { BooleanInput, coerceBooleanProperty } from '@angular/cdk/coercion';
import { NgForOf, NgIf } from '@angular/common';

export type DscAlertVariant = 'success' | 'danger' | 'warning' | 'info';

@Component({
  selector: 'dsc-alert',
  standalone: true,
  imports: [MatIconModule, NgIf, NgForOf],
  templateUrl: './dsc-alert.component.html',
  styleUrls: ['./dsc-alert.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class DscAlertComponent implements OnInit {

  @Input()
  title?: string;

  @Input()
  get message() {
    return this._message;
  }

  set message(valor: string) {
    this._message = valor;
  }

  _message: string = '';

  @Input()
  get list() {
    return this._list;
  }

  set list(values: string[]) {
    this._list = values;
  }

  _list: string[] = [];

  @Input()
  get showIcon(): boolean {
    return this._showIcon;
  }

  set showIcon(value: BooleanInput) {
    this._showIcon = coerceBooleanProperty(value);
  }

  private _showIcon = false;

  @Input()
  get dismissible(): boolean {
    return this._dismissible;
  }

  set dismissible(value: BooleanInput) {
    this._dismissible = coerceBooleanProperty(value);
  }

  private _dismissible = false;

  @Input()
  get variant(): DscAlertVariant {
    return this._variant;
  }

  set variant(variant: DscAlertVariant) {
    this._variant = variant;
    this.matIconName = variant;
  }

  _variant: DscAlertVariant = 'info';

  @Output() linkFunction = new EventEmitter<void>();

  ngOnInit(): void {
    if(!this.message && !this.list) {
      this._message = 'Mensagem obrigatória.';
    }
  }

  get matIconName(): string {
    return this._matIconName;
  }

  set matIconName(value: string) {
    switch (value) {
      case 'info':
        this._matIconName = 'info';
        break;
      case 'danger':
        this._matIconName = 'error';
        break;
      case 'success':
        this._matIconName = 'check_circle';
        break;
      case 'warning':
        this._matIconName = 'warning';
        break;
    }
  }

  _matIconName: string = 'info';

  classes = '';

  get ariaLabel(): string {
    switch (this.variant) {
      case 'info':
        return 'Informação';
      case 'danger':
        return 'Perigo';
      case 'success':
        return 'Sucesso';
      case 'warning':
        return 'Aviso';
    }
  }

  handleClick(event: Event) {
    const target = event.target as HTMLElement;

    if (target.tagName === 'A' && target.classList.contains('custom-link')) {
      event.preventDefault();
      this.linkFunction.emit();
    }
  }

}
